import Header from '../components/Header'
import Router from 'next/router'

const handler = () =>
  Router.push({
    pathname: '/about',
    query: { name: 'Zeit' }
  })

const Index = () => (
  <div>
    <Header/>
    <p>Hello Next.js</p>
    <div>
      Click <span onClick={handler}>here</span> to read more
    </div>
  </div>
)

export default Index



